import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Chatroom from "../views/Chatroom.vue"
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/chatroom",
    name: "Chat",
    component: Chatroom
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
